<template lang="html">
  <div>
    <content-loader
      v-if="loading && stateResizeScreen == 3"
  		:height="285"
  		:width="500"
  		:speed="2"
  		primaryColor="#f3f3f3"
  		secondaryColor="#dcdcdc"
  	>
      <rect x="4.63" y="34.61" rx="0" ry="0" width="69.09" height="8.55" />
  		<rect x="4.63" y="7.61" rx="0" ry="0" width="108.57" height="15.2" />
  		<rect x="286.63" y="44.61" rx="0" ry="0" width="0" height="0" />
  		<rect x="5.08" y="50.61" rx="0" ry="0" width="110.72" height="23.61" />
  		<rect x="416.63" y="216.61" rx="0" ry="0" width="0" height="0" />
  		<rect x="5.08" y="78.61" rx="0" ry="0" width="110.72" height="23.61" />
  		<rect x="138.63" y="49.61" rx="0" ry="0" width="110.72" height="23.61" />
  		<rect x="138.63" y="78.61" rx="0" ry="0" width="110.72" height="23.61" />
  		<rect x="5.63" y="114.61" rx="0" ry="0" width="243" height="202.8" />
  		<rect x="274.63" y="52.61" rx="0" ry="0" width="29.91" height="42.12" />
  		<rect x="273.63" y="34.61" rx="0" ry="0" width="69.09" height="8.55" />
  		<rect x="306.63" y="52.61" rx="0" ry="0" width="29.91" height="41.99" />
  		<rect x="338.63" y="52.61" rx="0" ry="0" width="29.91" height="41.99" />
  		<rect x="370.63" y="52.61" rx="0" ry="0" width="29.91" height="41.99" />
  		<rect x="402.63" y="52.61" rx="0" ry="0" width="29.91" height="41.99" />
  		<rect x="273.63" y="115.61" rx="0" ry="0" width="223.56" height="74.82" />
  	</content-loader>
    <content-loader
      v-if="loading && stateResizeScreen == 2"
  		:height="736"
  		:width="599"
  		:speed="2"
  		primaryColor="#f3f3f3"
  		secondaryColor="#ecebeb"
  	>
    <rect x="13.78" y="149.83" rx="0" ry="0" width="267.92" height="45.36" />
    <rect x="13.89" y="211.83" rx="0" ry="0" width="569.24" height="425.28" />
    <rect x="312.78" y="149.83" rx="0" ry="0" width="267.92" height="45.36" />
    <rect x="13.78" y="90.83" rx="0" ry="0" width="267.92" height="45.36" />
    <rect x="311.78" y="90.83" rx="0" ry="0" width="267.92" height="45.36" />
  	</content-loader>
    <content-loader
      v-if="loading && stateResizeScreen == 1"
  		:height="736"
  		:width="599"
  		:speed="2"
  		primaryColor="#f3f3f3"
  		secondaryColor="#ecebeb"
  	>
      <rect x="13.78" y="226.83" rx="0" ry="0" width="267.92" height="45.36" />
  		<rect x="13.89" y="288.83" rx="0" ry="0" width="569.24" height="425.28" />
  		<rect x="312.78" y="226.83" rx="0" ry="0" width="267.92" height="45.36" />
  		<rect x="13.78" y="167.83" rx="0" ry="0" width="267.92" height="45.36" />
  		<rect x="311.78" y="167.83" rx="0" ry="0" width="267.92" height="45.36" />
  	</content-loader>
    <div class="student__progress" v-if="!loading">
      <div class="view__header">
        <div class="view__header__title">
          <div>
            <h2>Student Progress Report</h2>
            <div class="blue__circle"></div>
          </div>
          <h2 class="hidden-sm-and-down" style="color:rgb(112, 185, 247);" v-if="handleStdType">Trial Student</h2>
        </div>
      </div>
      <!-- SCREEN SIZE >= 1024 -->
      <div class="student__progress__content" v-if="stateResizeScreen == 3">
        <div class="flex--column width--50perc">
          <StudyLevel />
          <ProgressBar />
        </div>
        <CertificatePlan />
        <!-- <CertificatePlanProfile /> -->
      </div>
      <!-- SCREEN SIZE <= 768 -->
      <div class="student__progress--768" v-if="stateResizeScreen == 2">
        <StudyLevel />
        <ProgressBar />
        <CertificatePlan />
      </div>
      <!-- SCREEN SIZE <= 599 -->
      <div class="student__progress--599" v-if="stateResizeScreen == 1">
        <StudyLevel />
        <ProgressBar />
        <CertificatePlan />
      </div>
    </div>
  </div>
</template>

<script>
import { ContentLoader } from 'vue-content-loader';

import StudyLevel from '../../student-components/studentProgressReport/StudyLevel'
import ProgressBar from '../../student-components/studentProgressReport/ProgressBar'
import CertificatePlan from '../../student-components/studentProgressReport/CertificatePlan'
// import CertificatePlanProfile from '../../student-components/studentProgressReport/CertificatePlanProfile'

export default {
  components: {
    StudyLevel,
    ProgressBar,
    CertificatePlan,
    // CertificatePlanProfile,
    ContentLoader
  },
  data () {
    return {

    }
  },
  computed: {
    loading () {
      return this.$store.state.loadingTrue
    },
    stateResizeScreen () {
      const screen = this.$store.state.stateResizeScreen
      if (screen == null) {
        return 0
      } else {
        return screen
      }
    },
    checkCall1Call2 () {
      const data = this.$store.state.stateCall1Call2Error

      if (data == "Error") {
        return this.$forceUpdate()
      } else {
        return "ok"
      }
    },
    handleStdType () {
       const data = this.$store.state.DataAdditional.type

       if (data == "regular") {
         return false
       } else {
         return true
       }
    }
  },
  mounted () {
    this.$nextTick( () => {
       window.addEventListener('resize', this.getWindowWidth);
       window.addEventListener('resize', this.getWindowHeight);

       this.getWindowWidth()
       this.getWindowHeight()
    });
  },
  created () {
    const studentUsername = {
      username: this.$store.state.DataAdditional.username
    }
    this.$store.dispatch('GET_CALL1_CALL2', studentUsername)
    this.$store.commit('buttonLoading');
  },
  methods: {
    getWindowWidth(event){
       this.windowWidth = document.documentElement.clientWidth;

       if (this.windowWidth <= 599){
       //this for resolution phone only
       const resizeScreen = 1
       this.$store.commit('commitResizeScreen', resizeScreen)
       }else if(this.windowWidth <= 768){
          //this for resolution Ipad Potrait
          const resizeScreen = 2
          this.$store.commit('commitResizeScreen', resizeScreen)
       }else if(this.windowWidth >= 1024){
          //this for resolution Dekstop-up
          const resizeScreen = 3
          this.$store.commit('commitResizeScreen', resizeScreen)
       }
    },
    getWindowHeight(event){
       this.windowHeigt = document.documentElement.clientHeight;
    }
  },
  beforeDestroy() {
     window.removeEventListener('resize', this.getWindowWidth);
     window.removeEventListener('resize', this.getWindowHeight);
  }
}
</script>

<style lang="scss">
  @import './StudentProgressReport.scss';
</style>



